@import url(https://rsms.me/inter/inter.css);

.popover{max-width:50%;min-width:500px}


.popover-container{padding:0.7em}.popover-title{font-weight:600 !important}.popover-buttons>*{margin-right:1em !important}

.show-and-hide-main{font-family:"Inter", sans-serif;position:relative;height:110px;margin-bottom:1.5em}.show-and-hide-text{position:relative;line-height:110px;color:black;font-size:8em;font-weight:800;letter-spacing:-9px;will-change:transform, opacity;overflow:hidden}.show-and-hide-text>div{overflow:hidden}

.spring-3d-wrapper{display:flex;align-items:center;justify-content:center}.spring-3d-content{width:200px;height:200px;background:grey;border-radius:5px;box-shadow:0px 10px 30px -5px rgba(0,0,0,0.3);transition:box-shadow 0.5s;will-change:transform;border-radius:100%}.spring-3d-image{border-radius:100% !important;width:200px;height:200px}.spring-3d-content:hover{box-shadow:0px 30px 100px -10px rgba(0,0,0,0.4)}

.hide-axis .fc-timegrid-slot-label{display:none}.fc-day-today{background-color:inherit !important}.fc-event-title{font-size:0.7vw}.fc-event{border-width:0.12vw}

div.delete-field-button{display:flex;align-items:center}





.pagination-modal{overflow-y:scroll}.pagination-modal-paper{margin:1em}

div.Page{display:flex;height:100%;justify-content:center;padding:1em}div.Page>div.content{max-width:900px;text-align:left;width:900px}

.schedule-wrapper{overflow-x:auto}.fc-event{cursor:pointer}.adjacent{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;flex-direction:row;flex-wrap:nowrap}.calendar-width{width:350px}.fc-event-title,.fc-event-time{font-size:0.7em !important}.calendar-title{padding-top:1em;padding-bottom:1em}.schedule-header-row{margin-left:-8em;padding-left:8em;margin-right:-8em;padding-right:8em;margin-top:1em;background-color:#f2f2f2}.schedule-time-axis-wrapper{display:flex;flex-direction:row}.time-slot{display:flex;justify-content:flex-end;height:50px;padding-right:0.5em}.left-time-axis{background-color:#f2f2f2;display:flex;flex-direction:column;width:6em;padding-top:28px}


.harmony-stepper-root{width:100%;display:flex;flex-direction:column;height:100%}.harmony-stepper-button{margin-right:1em !important}.harmony-stepper-content{margin-top:1em;margin-bottom:1em;height:100%}

.App{text-align:center;display:flex;flex-direction:column}.content-container{flex:1 0 auto}

.app-footer{background-color:#e3e3e3 !important;color:#121212 !important;font-size:1em;flex-shrink:0}

.toolbar{justify-content:space-between}.org-logo{width:60px;height:auto}.toolbar-group{align-items:center;display:flex;flex-direction:row}.links>*{margin-left:1em !important;color:white !important}.title>*{margin-right:1em !important}.title{text-decoration:none;color:white}

.change-cursor{cursor:pointer}

.department-calendar-width{width:1400px !important}

.change-cursor{cursor:pointer}.add-non-teaching-activity-button{margin:1em}




.hidden{display:none !important}

.import-button{margin-right:1em !important;color:white !important}

.schedulizer-header{display:flex !important;flex-direction:row;justify-content:space-between;align-items:center}


.schedule-toolbar,.schedule-toolbar>div{display:flex;justify-content:space-between}.schedule-toolbar{position:-webkit-sticky;position:sticky;top:0;background-color:#f2f2f2;z-index:3;padding:1em;margin-left:-2em;margin-right:-2em;margin-bottom:1.5em;min-height:10vh}.toolbar-left{width:35%}.toolbar-right{width:20%}

.searchbar{width:300px}

.semester-selector{display:flex;text-align:center;align-items:center}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}html{overflow-x:hidden}html,body,#root,.App{height:100%}

